<template>
	<div v-if="page_loaded">
		<aio-login-form
			:action="nonce"
			v-on:handle-submit="handleSubmit"
		>
			<template v-slot:title>
				Google reCAPTCHA
			</template>

			<template v-slot:form-fields>
				<tr>
					<th scope="row">
						<label for="enable">Enable</label>
					</th>

					<td>
						<aio-login-toggle
							id="enable"
							name="aio_login_google_recaptcha_enable"
							v-on:toggle-input="handleToggleValue"
							:enabled="form_data.enabled"
						/>
					</td>
				</tr>

				<tr v-if="form_data.enabled">
					<th scope="row">
						<label for="version">Version</label>
					</th>

					<td>
						<select
							name="aio_login_google_recaptcha_version"
							id="version"
							v-model="form_data.version"
							class="regular-text"
						>
							<option value="v2">v2</option>
							<option value="v3">v3</option>
						</select>
					</td>
				</tr>

				<tr v-if="form_data.enabled && 'v2' === form_data.version">
					<th scope="row">
						<label for="site_v2_key">Site Key</label>
					</th>

					<td>
						<input
							type="text"
							name="aio_login_google_recaptcha_site_v2_key"
							id="site_v2_key"
							v-model="form_data.v2_site_key"
							class="regular-text"
						/>
					</td>
				</tr>

				<tr v-if="form_data.enabled && 'v2' === form_data.version">
					<th scope="row">
						<label for="site_v2_secret">Secret Key</label>
					</th>

					<td>
						<input
							type="text"
							name="aio_login_google_recaptcha_site_v2_secret"
							id="site_v2_secret"
							v-model="form_data.v2_secret_key"
							class="regular-text"
						/>
					</td>
				</tr>

				<tr v-if="form_data.enabled && 'v2' === form_data.version">
					<th scope="row">
						<label for="theme">Theme</label>
					</th>

					<td>
						<select
							name="aio_login_google_recaptcha_theme"
							id="theme"
							v-model="form_data.theme"
							class="regular-text"
						>
							<option value="light">Light</option>
							<option value="dark">Dark</option>
						</select>
					</td>
				</tr>

				<tr v-if="form_data.enabled && 'v3' === form_data.version">
					<th scope="row">
						<label for="site_v3_key">Site Key</label>
					</th>

					<td>
						<input
							type="text"
							name="aio_login_google_recaptcha_site_v3_key"
							id="site_v3_key"
							v-model="form_data.v3_site_key"
							class="regular-text"
						/>
					</td>
				</tr>

				<tr v-if="form_data.enabled && 'v3' === form_data.version">
					<th scope="row">
						<label for="site_v3_secret">Secret Key</label>
					</th>

					<td>
						<input
							type="text"
							name="aio_login_google_recaptcha_site_v3_secret"
							id="site_v3_secret"
							v-model="form_data.v3_secret_key"
							class="regular-text"
						/>
					</td>
				</tr>

				<tr v-if="form_data.enabled && 'v3' === form_data.version">
					<th scope="row">
						<label for="threshold">Threshold</label>
					</th>

					<td>
						<select
							name="aio_login_google_recaptcha_threshold"
							id="threshold"
							v-model="form_data.threshold"
							class="regular-text"
						>
							<option value="0.1">0.1</option>
							<option value="0.2">0.2</option>
							<option value="0.3">0.3</option>
							<option value="0.4">0.4</option>
							<option value="0.5">0.5</option>
						</select>
					</td>
				</tr>
			</template>
		</aio-login-form>

		<aio-login-snackbar
			:message="snackbar.message"
			:duration="snackbar.duration"
			v-if="snackbar.show"

			v-on:close="handleCloseSnackbar"
		/>
	</div>
</template>

<script>

export default {
	name: 'grecaptcha',

	data: () => ( {
		page_loaded: false,

		nonce: '',

		namespace: 'aio-login/grecaptcha',

		form_data: {
			enabled: false,
			version: 'v2',
			v2_site_key: '',
			v2_secret_key: '',
			theme: 'light',
			v3_site_key: '',
			v3_secret_key: '',
			threshold: '0.5',
		},

		snackbar: {
			message: '',
			duration: 3000,
			show: false,
		}
	} ),

	methods: {
		handleToggleValue( value ) {
			this.form_data.enabled = value;
		},

		handleSubmit( e ) {
			axios.post( this.namespace + '/save-settings', this.form_data )
				.then( response => {

					this.snackbar.message = response.data.message;
					this.snackbar.show = true;
				} )
				.catch( error => {

				} );
		},

		handleCloseSnackbar() {
			this.snackbar.show = false;
		}
	},

	mounted() {
		this.$nextTick( () => {
		} );

		axios.get( this.namespace + '/get-settings' )
			.then( response => {
				this.form_data.enabled       = response.data.enabled;
				this.form_data.version       = response.data.version;
				this.form_data.v2_site_key   = response.data.v2_site_key;
				this.form_data.v2_secret_key = response.data.v2_secret_key;
				this.form_data.theme         = response.data.theme;
				this.form_data.v3_site_key   = response.data.v3_site_key;
				this.form_data.v3_secret_key = response.data.v3_secret_key;
				this.form_data.threshold     = response.data.threshold;
				this.form_data._wpnonce      = response.data.nonce;
				this.page_loaded             = true;
			} )
			.catch( error => {
			} );
	}
}
</script>

